<template>
    <background :style="{width: pr.ww + 'px'}">
      <topTitle>ÂèëÂ∏ñ</topTitle>
      <div>
        <button @click="pasteFromClipboard" class="custom-button">‰ªéÂâ™Ë¥¥ÊùøÂØºÂÖ•</button>
      </div>
        <!-- Ê†áÈ¢òËæìÂÖ• -->
        <div>
          <input v-model="post.title" type="text" placeholder="Â∏ñÂ≠êÊ†áÈ¢ò" style="width: 40%;font-size: 3vh" />
        </div>
<br>
        <CoverBox>
          <img :src="post.cover" alt="Â∞ÅÈù¢" class="subCover">
        </CoverBox>
        <input type="file" id="fileInputCover" @change="handleCoverUpload" style="display: none;" />
        <label for="fileInputCover" class="custom-button" style="background-color:green;width: 89%;">ÈÄâÊã©Â∞ÅÈù¢</label>

        <button @click="addTextLine" style="width: 15%" class="custom-button">Ê∑ªÂä†Ê≠£ÊñáÊÆµËêΩ</button>
      <topTextBox>
        <!-- Ê≠£ÊñáËæìÂÖ•ÔºåÊåâË°åÂàÜÈöî -->
        <div v-for="(item, index) in tempTopText" :key="index">
            <div style="display: flex;flex-direction: row;">
          <button @click="removeParagraph(index)" style="height: 2%;" class="custom-button">x</button>
          <textarea v-model="tempTopText[index]" type="text" @input="updateText(index, $event.target.value)" placeholder="Ê≠£ÊñáÂÜÖÂÆπ" style="width: 100%;font-size: 2vh;background-color: transparent;border: none;word-wrap: break-word;height: auto;" ></textarea>
            </div>
        </div>
      </topTextBox>
<br>
        <button @click="addEntry" style="width: 91%;" class="custom-button">Ê∑ªÂä†Êù°ÁõÆ</button>
      <!-- ÈÅçÂéÜ entris Êï∞ÁªÑ -->
      <div>
        <div v-for="(entry, entryIndex) in post.entris" :key="entry.id">
          <entryBlock class="need-border" style="margin-bottom: 2vh;filter: drop-shadow(0 0 15px #666)">
            <!-- Êù°ÁõÆÊ†áÈ¢ò -->
            <subTitle class="subText need-border" :style="{'background-color': entry.title.color,'filter': 'drop-shadow(0 0 15px #666)','border-radius':15+'px'}">
              <input v-model="entry.title.text" type="text" placeholder="Êù°ÁõÆÊ†áÈ¢ò" class="inputBox" />
              <input type="color" v-model="entry.title.color" class="colorPanel"/>
            </subTitle>
            <!-- Êù°ÁõÆÊ≠£Êñá -->
            <subText class="subText need-border horizontal-scroll-container" :style="{'background-color': entry.text.color,'margin-top': 2+'vh','filter': 'drop-shadow(0 0 15px #666)','border-radius':15+'px'}">
              <textarea v-model="entry.text.text" placeholder="Êù°ÁõÆÂÜÖÂÆπ" class="inputBox"></textarea>
              <input type="color" v-model="entry.text.color" class="colorPanel"/>
            </subText>
            <!-- Êù°ÁõÆÈ°∫Â∫è -->
            <order :style="{'background-color': entry.order.color,'margin-top': 2+'vh','filter': 'drop-shadow(0 0 15px #666)','border-radius':15+'px'}">
              <input v-model="entry.order.text" type="text" placeholder="Êù°ÁõÆÈ°∫Â∫è" class="inputBox" />
              <input type="color" v-model="entry.order.color" class="colorPanel"/>
            </order>
            <!-- Êù°ÁõÆÊ†áÁ≠æ -->
            <subTags style="display: flex;flex-direction: row;flex-wrap: wrap;">
              <div v-for="(tag, tagIndex) in entry.tags" :key="tagIndex" >
                <tagBox :style="{'background-color': tag.color,'border-radius':15+'px'}">
                  <input v-model="tag.text" type="text" placeholder="Ê†áÁ≠æ" style="width: 8vw;height: 2vh;background-color: transparent; border: none;font-size: 1.5vh" />
                  <input type="color" v-model="entry.tags[tagIndex].color" class="colorPanel" style="width: 1vw;height: 2vh"/>
                  <button @click="removeTag(entryIndex, tagIndex)" style="height: 2vh;background-color: transparent;border:2vh solid transparent" >X</button>
                </tagBox>
              </div>
            </subTags>
              <button @click="addTag(entryIndex)" style="width: 8%;background-color:green;" class="custom-button">New tag</button>

            <!-- ÂõæÁâá -->
            <div style="display: flex;flex-wrap: wrap; ">
            <div v-for="(cover, index) in entry.covers" :key="cover" class="need-border" >
              <subCoverBox>
                <img :src="cover" alt="ÂÜÖÂÆπÂõæÁâá" @click="delSubCover(entryIndex,index)" class="subCover">
              </subCoverBox>
            </div>
            </div>
              <input type="file" :id="'fileInputSub'+entryIndex" @change="(event) => handleFileUpload(event, entryIndex)" style="display: none;" multiple />
              <label :for="'fileInputSub'+entryIndex" class="custom-button" style="background-color:green;">ÈÄâÊã©ÂõæÁâá</label>
              <button @click="removeEntry(entryIndex)" class="custom-button" style="background-color:#000;">Âà†Èô§Êù°ÁõÆ{{entryIndex+1}}</button>
          </entryBlock>
    </div>
</div>
<br>
    <!-- ‰øùÂ≠òÊåâÈíÆ -->
    <button @click="savePost" style="width: 91%;" class='custom-button'>‰øùÂ≠òÂ∏ñÂ≠ê</button>
    <br><br><br><br><br><br><br><br>
    </background>
  </template>
  
  <script>
  export default {
    
    data() {
      return {
        tempTopText:[],
        post: {
          title: '',
          text: '',
          cover: 'favicon.ico',
          tags:[],
          entris: [
            // ÂàùÂßãÊù°ÁõÆÊï∞ÊçÆÔºåÂèØ‰ª•Ê†πÊçÆÈúÄË¶ÅÊ∑ªÂä†Êõ¥Â§öÂ≠óÊÆµ
            {
              id: 1,
              title: { text: '', color: '#fff6a6' },
              covers: [],
              order: { text: '', color: '#fff6a6' },
              tags: [{ text: '', color: '#fff6a6' }],
              text: { text: '', color: '#fff6a6' }
            }
          ]
        },
        postDefault: {
          title: '',
          text: '',
          cover: 'favicon.ico',
          tags:[],
          entris: [
            // ÂàùÂßãÊù°ÁõÆÊï∞ÊçÆÔºåÂèØ‰ª•Ê†πÊçÆÈúÄË¶ÅÊ∑ªÂä†Êõ¥Â§öÂ≠óÊÆµ
            {
              id: 1,
              title: { text: '', color: '#fff6a6' },
              covers: [],
              order: { text: '', color: '#fff6a6' },
              tags: [{ text: '', color: '#fff6a6' }],
              text: { text: '', color: '#fff6a6' }
            }
          ]
        },
        displayIndexes: {}, // Áî®‰∫éÊéßÂà∂Â∞ÅÈù¢ÂõæÁâáÊòæÁ§∫ÁöÑÁ¥¢Âºï
      };
    },
    methods: {
      async pasteFromClipboard() {
      try {
        const clipboardText = await navigator.clipboard.readText();
        this.post = JSON.parse(clipboardText);
        console.log(this.post);
      } catch (error) {
        alert('ËØªÂèñÂâ™Ë¥¥ÊùøÂ§±Ë¥•:', error);
      }
    },
      // Êõ¥Êñ∞Â∏ñÂ≠êÊ≠£ÊñáÁöÑÊüêË°åÊñáÊú¨
      updateText(index, value) {
        this.post.text = this.post.text.split('\n').map((v, i) => {
          return i === index ? value : v;
        }).join('\n');
      },
      // Ê∑ªÂä†Êñ∞ÁöÑÊ≠£ÊñáË°å
      addTextLine() {
        this.tempTopText.push('')
      },
      // Ê∑ªÂä†Êñ∞ÁöÑÊù°ÁõÆ
      addEntry() {
        this.post.entris.push({
          id: this.post.entris.length + 1,
          title: { text: '', color: '#fff6a6' },
          covers: [],
          order: { text: '', color: '#fff6a6' },
          tags: [{ text: '', color: '#fff6a6' }],
          text: { text: '', color: '#fff6a6' }
        });
      },
      // Ê∑ªÂä†Êñ∞ÁöÑÊ†áÁ≠æÂà∞ÊåáÂÆöÊù°ÁõÆ
      addTag(entryIndex) {
        this.post.entris[entryIndex].tags.push({ text: '', color: '#fff6a6' });
      },
      // Âà†Èô§‰∏ÄÊÆµÊ≠£Êñá
      removeParagraph(index) {
        this.tempTopText.splice(index, 1);
      },
      // ‰ªéÊåáÂÆöÊù°ÁõÆ‰∏≠Âà†Èô§Ê†áÁ≠æ
      removeTag(entryIndex, tagIndex) {
        this.post.entris[entryIndex].tags.splice(tagIndex, 1);
      },
      // Âà†Èô§Ê†áÁ≠æ
      removeEntry(index) {
        this.post.entris.splice(index, 1);
      },
      // ÂàáÊç¢Â∞ÅÈù¢ÂõæÁâáÊòæÁ§∫
      nextCover(entryIndex) {
        // ÁÆÄÂçïÁöÑÈÄªËæëÔºåÂÆûÈôÖÂ∫îÁî®‰∏≠ÂèØËÉΩÈúÄË¶ÅÊõ¥Â§çÊùÇÁöÑÁä∂ÊÄÅÁÆ°ÁêÜ
        if (this.displayIndexes[entryIndex] === undefined) {
          this.displayIndexes[entryIndex] = 0;
        } else {
          this.displayIndexes[entryIndex]++;
        }
      },
      // ‰øùÂ≠òÂ∏ñÂ≠ê
      savePost() {
        this.post.text=this.tempTopText.join('\n');
        console.log('Â∏ñÂ≠ê‰øùÂ≠ò:', this.post);
            // ‰ΩøÁî®Clipboard APIÂ§çÂà∂JSONÂ≠óÁ¨¶‰∏≤Âà∞Ââ™Ë¥¥Êùø
        if (navigator.clipboard && navigator.clipboard.writeText) {
        const jsonStr = JSON.stringify(this.post);
        navigator.clipboard.writeText(jsonStr).then(() => {
            console.log('JSONÂ≠óÁ¨¶‰∏≤Â∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø');
            alert('ÂØºÂá∫ÊàêÂäü!');
            // Â§çÂà∂ÊàêÂäüÁöÑÊìç‰Ωú
            this.post=this.postDefault;
            this.$emit('generateNewPost',this.post);//to explore subwin
        }).catch(err => {
            console.error('Â§çÂà∂Âà∞Ââ™Ë¥¥ÊùøÊó∂Âá∫ÈîôÔºö', err);
            alert('SomethongWrong');
            // Â§çÂà∂Â§±Ë¥•ÁöÑÊìç‰Ωú
        });
        } else {
        console.error('Clipboard API ‰∏çÊîØÊåÅ');
            alert('Clipboard API ‰∏çÊîØÊåÅ');
        // Clipboard API ‰∏çÊîØÊåÅÊó∂ÁöÑÊìç‰Ωú
        }
      },
      handleFileUpload(event,entryIndex) {
        const files = event.target.files;
        if (!files.length) {
          return;
        }
        // Âæ™ÁéØÂ§ÑÁêÜÊØèÂº†ÂõæÁâá
        for (let i = 0; i < files.length; i++) {
          const file = files[i];
          const reader = new FileReader();
          reader.onload = (e) => {
            const base64Image = e.target.result;
            this.post.entris[entryIndex].covers.push(base64Image);
          };
          reader.readAsDataURL(file);
        }
      },
      handleCoverUpload(event) {
        const files = event.target.files;
        if (!files.length) {
          return;
        }
        
        // ËØªÂèñÊñá‰ª∂Âπ∂ËΩ¨Êç¢‰∏∫base64
        const file = files[0];
        const reader = new FileReader();
        reader.onload = (e) => {
          const base64Image = e.target.result;
          this.post.cover=base64Image; // Â∞Übase64ÁºñÁ†ÅÊ∑ªÂä†Âà∞ÂàóË°®‰∏≠
        };
        reader.readAsDataURL(file);
      },
      delSubCover(entryIndex,index){
        this.post.entris[entryIndex].covers.splice(index,1)
      }
    }
  };
  </script>
  <script setup>
import {defineProps} from 'vue'
const pr = defineProps(['ww'])

</script>
