<template>
  <div id="chat-app" :style="{ width: pr.ww + 'px', height: pr.wh + 'px', borderRadius:pr.br, backgroundColor: pr.bc}">
    <textarea readonly v-if="replying" class="title" :style="{backgroundColor:pr.bc,borderRadius:pr.br}">ÂØπÊñπÊ≠£Âú®ËæìÂÖ•...</textarea>
    <textarea readonly v-else class="title" :style="{backgroundColor:pr.bc,borderRadius:pr.br}">ÂêëÂØº</textarea>
    <div class="chat-window" ref="chatWindow">
      <div v-for="(message, index) in messages" :key="index" :class="['message', message.role]">
        <bub :class="['bub', message.role]">
          <msg v-html="getProcessedContent(message.content)"></msg>
        </bub>
        <button-copy-box v-if="message.role=='assistant'||message.role=='tool'" :class="[message.role]">
          <buttonCopy @click="copyMessage(message.content)" icon-html='<i class="fa-regular fa-copy"></i>' r="4" size="0.7" style="margin-left: 5%;"></buttonCopy>
        </button-copy-box>
      </div>
      <div class="message user">
        <bub :class="[userInputIdle?'':'bub', 'user']">
          <msg v-html="getProcessedContent(userInput)"></msg>
        </bub>
      </div>
      <br v-for="i in Array(10).fill(null)" :key="i">
    </div>
      <textarea class="inputBox" ref="inp" v-model="userInput" @keyup.enter="sendMessage" placeholder="Type a message..." @oninput="heightC=!heightC" :class="heightC?'ta':'tb'" style="height: 2cqh;"></textarea>
  </div>
</template>
<script>
import io from 'socket.io-client';
import buttonCopy from './CircleButton.vue';
export default {
  data() {
    return {
      markdownString: '# Marked in Vue component\n\nRendered by **marked**.',
      messages: [{role:'user',content:'‰Ω†Â•Ω'},{role:'assistant',content:'ÊÇ®Â•ΩÔºåÊàëÊòØÊÇ®ÁöÑÊóÖË°åË¥üË¥£‰∫∫ÔºåÊàëÂ∞Ü‰∏∫ÊÇ®Êèê‰æõÊúÄ‰Ω≥ÁöÑÊóÖÊ∏∏ËßÑÂàí~'},{role:'tool',content:'ÊàëÊòØÊóÖË°åË¥üË¥£‰∫∫ÁöÑÂä©ÊâãÔºåÈÅáÂà∞ËæÉ‰∏∫‰∏ì‰∏öÁöÑÈóÆÈ¢òÔºåÊàë‰ºöÂ∞ΩÊàëÊâÄËÉΩÂçèÂä©Ë¥üË¥£‰∫∫ÁªôÂá∫Ëß£Èáä'}],
      userInput: '',
      heightC:false,
      replying:false
    };
  },
  computed:{
      userInputIdle() {
        return this.userInput===''
      },
      renderedMarkdown() {
        return window.marked.parse(this.markdownString);
      }
  },
  mounted() {
    console.log(process.env.VUE_APP_AI_CHAT_URL);
    this.socket = io(process.env.VUE_APP_AI_CHAT_URL);
    this.socket.on('state', (data) => {
      if(data.state){
        this.replying=true
      }else{
        this.replying=false;
      }
    });
    this.socket.on('assistant_response', (data) => {
      /*
      if(!this.replying){
        this.messages.push({role:'assistant',content:''})
        this.replying=true
      }
      this.messages[this.messages.length-1].content=data.assistant_response;//*/
      if(data.assistant_response.role==='post'){
        console.log('chatBox findPost');
        console.log(data.assistant_response);
        this.$emit('findPost',data.assistant_response)
        return;
      }
      if(data.assistant_response.content.trim()==='')return;
      this.messages.push(data.assistant_response)
      console.log(data.assistant_response);
      this.$nextTick(() => {
        this.scrollToBottom();
      });
    });
  },
  methods: {
    scrollToBottom() {
      const chatWindow = this.$refs.chatWindow;
      chatWindow.scrollTo(0,1E20)
    },
    sendMessage() {
      if(this.replying)return;
      this.messages.push({ role: 'user', content: this.userInput });
      this.socket.emit('message', this.userInput);
      this.userInput = '';
      this.replying = true;
      this.$nextTick(() => {
        this.scrollToBottom();
      });
    },
    getProcessedContent(text){
      return window.marked.parse(text);
    },
    copyMessage(text){
        if (navigator.clipboard && navigator.clipboard.writeText) {
        navigator.clipboard.writeText(text).then(() => {
            // Â§çÂà∂ÊàêÂäüÁöÑÊìç‰Ωú
        }).catch(err => {
            console.error('Â§çÂà∂Âà∞Ââ™Ë¥¥ÊùøÊó∂Âá∫ÈîôÔºö', err);
            alert('Â§çÂà∂Âà∞Ââ™Ë¥¥ÊùøÊó∂Âá∫Èîô');
            // Â§çÂà∂Â§±Ë¥•ÁöÑÊìç‰Ωú
        });
        } else {
        console.error('Clipboard API ‰∏çÊîØÊåÅ');
            alert('Clipboard API ‰∏çÊîØÊåÅ');
        // Clipboard API ‰∏çÊîØÊåÅÊó∂ÁöÑÊìç‰Ωú
        }
    }
  }
};
</script>

<script setup>
import {defineProps} from 'vue'
const pr = defineProps(['ww','wh','bc','br'])
</script>

<style scoped>

#chat-app {
  z-index: 10;
  box-sizing: border-box;
  width: 98%;
  height: 98%;
  position: relative;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  padding: 1%;
  top: 5px; /* Ë∞ÉÊï¥Èò¥ÂΩ±‰ΩçÁΩÆ‰ª•Á°Æ‰øùÂÆÉÂú®ÂÖÉÁ¥†ÁöÑÈ°∂ÈÉ® */
  bottom: -5px; /* Ë∞ÉÊï¥Èò¥ÂΩ±‰ΩçÁΩÆ‰ª•Á°Æ‰øùÂÆÉÂú®ÂÖÉÁ¥†ÁöÑÂ∫ïÈÉ® */
  left: 5px; /* Ë∞ÉÊï¥Èò¥ÂΩ±‰ΩçÁΩÆ‰ª•Á°Æ‰øùÂÆÉÂú®ÂÖÉÁ¥†ÁöÑÂ∑¶‰æß */
  right: -5px; /* Ë∞ÉÊï¥Èò¥ÂΩ±‰ΩçÁΩÆ‰ª•Á°Æ‰øùÂÆÉÂú®ÂÖÉÁ¥†ÁöÑÂè≥‰æß */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  user-select: none;
}

.chat-window {
  flex: 1;
  position: relative;
  top: 7%;
  max-height: 93%;
  overflow-y: scroll;
  justify-content: center;
  align-items: flex-end;
}
::-webkit-scrollbar {
  display: none;
}
.title {
  z-index: 100;
  padding-top: 1%;
  height: 7%;
  width: 100%;
  position: absolute;
  text-align: center;
  resize: none;
  outline: none;
  font-size: 3cqh;
  font-weight: lighter;
  background-color: lightgray;
  border: 0;
  font-family: 'titleFont';
  
}
.title:focus {
  outline: none
}
.bub {
  position: relative;
  display: inline-block;
  padding-left: 1.5%;
  padding-right: 1.5%;
  border-radius: 10px;
  filter: drop-shadow(0 0 3px #bbbbbb);
}
.bub.assistant {
  margin-left: 5%;
  margin-right: 38.2%;
  background-color: #fff;
}
.bub.user {
  margin-left: 38.2%;
  margin-right: 5%;
  background-color: #57d253;
}
.bub.tool {
  margin-left: 5%;
  margin-right: 38.2%;
  background-color: #ffc5c5;
}

.message {
  margin: 2% 0;
  font-size: 1.5vh;
}

.message.user {
  text-align: right;
}

.message.assistant {
  text-align: left;
}
inputBox {
  margin-bottom: 2vh;
  padding-bottom: 2vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  background-color: transparent;
}
textarea.inputBox {
  position: fixed;
  top:90%;
    word-wrap: break-word;
    white-space: break-spaces;
  border-radius: 13px;  
  width: 61.8%;
  padding-bottom: 1vh;
  padding-top: 1vh;
  padding-left: 1.3%;
  padding-right: 1.3%;
  align-self: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  overflow-x:hidden;
  resize: vertical;
  max-height: 40%;
  font-size: 2vh;

}
textarea::-webkit-scrollbar {
    display: none;
}
textarea:focus {
  outline: none;
}
</style>
